export const addToCart= (item) =>{
    return {
        item,
        type: 'ADD_TO_CART'
    }
}

export const addToWishlist =item => {
    return {
        item,
        type: 'ADD_TO_WISHLIST'
    }
}

export const addRemoveQuantity =(selectedQty, index)=> {
    return {
        selectedQty,
        index,
        type: 'ADD_REMOVE_QUANTITY'
    }
}